<template>
	<div id="dashboard">
		<ul class="collection with-header">
			<li class="collection-header"><h4>Employees List</h4></li>
			<li class="collection-item" v-for="employee in employees" :key="employee.id">
				<div class="chip">{{ employee.department  }} </div> 
				{{ employee.employee_id }} - {{ employee.first_name + ' ' + employee.last_name }}
				<router-link :to="generateLink(employee)" class="secondary-content"><i class="fa fa-eye"></i></router-link>
			</li>
		</ul>
		<div class="fixed-action-btn">
			<router-link to="/new" class="btn-floating btn-large blue">
				<i class="fa fa-plus"></i>
			</router-link>
		</div>
	</div>
</template>

<script>
import db from '../firebase/init'
export default {
  name: 'Dashboard',
  data () {
    return {
		employees: []			
    }
  }, 
  methods: {
	  generateLink(employee) {
		  return { name: 'view-employee', params: { employee_id: employee.employee_id } }
	  }
  },
  created() {
		db.collection('employees').orderBy('department').get().then(
			querySnapShot => {
				querySnapShot.forEach(doc => {
					let data = {
						id: doc.id,
						employee_id: doc.data().employee_id,
						first_name: doc.data().first_name,
						last_name: doc.data().last_name,
						position: doc.data().position,
						department: doc.data().department
					}
					this.employees.push(data)
				});				
			})
	}
}
</script>

<style lang="css" scoped>
</style>


