<template>
	<div id="edit-employee">
		<h3>Edit Employee</h3>
    <div class="row">
      <form class="col s12">
        <div class="row">
          <div class="input-field col s12">
            <input placeholder="* Employee ID" type="text" v-model="employee_id" disabled>
            <label for="employee_id" class="active">Employee ID</label>
          </div>
          <div class="input-field col s12">
            <input placeholder="* Firstname" type="text" v-model="first_name">
            <label for="first_name" class="active">First Name</label>
          </div>
          <div class="input-field col s12">
            <input placeholder="* Lastname" type="text" v-model="last_name">
            <label for="last_name" class="active">Last Name</label>
          </div>
          <div class="input-field col s12">
            <input placeholder="* Position" type="text" v-model="position">
            <label for="last_name" class="active">Position</label>
          </div>
          <div class="input-field col s12">
            <input placeholder="* Department" type="text" v-model="department">
            <label for="last_name" class="active">Department</label>
          </div>
        </div>
        <router-link to="/" class="btn grey">Cancel</router-link>
        <button class="btn blue" @click.prevent="saveEmployee">Save</button>
      </form>
    </div>
	</div>
</template>

<script>
import db from '../firebase/init'
export default {
  name: 'EditEmployee',
  data () {
    return {
      employee_id: null,
      first_name: null,
      last_name: null,
      department: null,
      position: null
    }
  },
  beforeRouteEnter (to, from, next) {
    db.collection('employees')
    .where('employee_id', '==', to.params.employee_id)
    .get()
    .then(querySnapShot => {
      querySnapShot.forEach(doc => {
        let data = doc.data();
        next(vm => {
          vm.employee_id = data.employee_id,
          vm.first_name = data.first_name,
          vm.last_name = data.last_name,
          vm.department = data.department,
          vm.position = data.position
        })
      })
    })
  },
  watch: {
    '$route':'retrieveEmployee'
  },
  methods: {
    retrieveEmployee() {
      db.collection('employees')
      .where('employee_id', '==', this.$router.params.employee_id)
      .get()
      .then(querySnapShot => {
        querySnapShot.forEach(doc => {
          let data = doc.data();
          this.employee_id = data.employee_id,
          this.first_name = data.first_name,
          this.last_name = data.last_name,
          this.department = data.department,
          this.position = data.position
        })
      })
    },
    saveEmployee() {
      db.collection('employees')
      .where('employee_id', '==', this.employee_id)
      .get()
      .then(querySnapShot => {
        querySnapShot.forEach(doc => {
          let data = {
            employee_id: this.employee_id,
            first_name: this.first_name,
            last_name: this.last_name,
            department: this.department,
            position: this.position
          }
          doc.ref.update(data)
          .then(docRef => this.$router.push({name: 'view-employee', params: { employee_id: this.employee_id }}))
        })
      })
    }
  },
}
</script>

<style lang="css" scoped>
</style>


