<template>
	<div id="new-employee">
		<h3>New Employee</h3>
    <div class="row">
      <form class="col s12">
        <div class="row">
          <div class="input-field col s12">
            <input placeholder="* Employee ID" type="text" v-model="employee_id">
            <label for="employee_id" class="active">Employee ID</label>
          </div>
          <div class="input-field col s12">
            <input placeholder="* Firstname" type="text" v-model="first_name">
            <label for="first_name" class="active">First Name</label>
          </div>
          <div class="input-field col s12">
            <input placeholder="* Lastname" type="text" v-model="last_name">
            <label for="last_name" class="active">Last Name</label>
          </div>
          <div class="input-field col s12">
            <input placeholder="* Position" type="text" v-model="position">
            <label for="last_name" class="active">Position</label>
          </div>
          <div class="input-field col s12">
            <input placeholder="* Department" type="text" v-model="department">
            <label for="last_name" class="active">Department</label>
          </div>
        </div>
        <router-link to="/" class="btn grey">Cancel</router-link>
        <button class="btn blue" @click.prevent="saveEmployee">Save</button>
      </form>
    </div>
	</div>
</template>

<script>
import db from '../firebase/init'
export default {
  name: 'NewEmployee',
  data () {
    return {
      employee_id: null,
      first_name: null,
      last_name: null,
      department: null,
      position: null
    }
  },
  methods: {
    saveEmployee() {
      let employee = {
        employee_id: this.employee_id,
        first_name: this.first_name,
        last_name: this.last_name,
        department: this.department,
        position: this.position
      }

      db.collection('employees').add(employee)
      .then(docRef => this.$router.push('/'))
    }
  },
}
</script>

<style lang="css" scoped>
</style>


