<template>
	<div id="view-employee">
		<ul class="collection with-header">
      <li class="collection-header"><h4>{{ first_name + ' ' + last_name }}</h4></li>
      <li class="collection-item">{{ employee_id }}</li>
      <li class="collection-item">{{ position }}</li>
      <li class="collection-item">{{ department }}</li>
    </ul>
    <router-link class="waves-effect waves-teal btn-small grey" to="/">Back</router-link>
    <button class="waves-effect waves-teal btn-small red" @click="deleteEmployee()">Delete</button>

    <div class="fixed-action-btn">
			<router-link :to="editLink" class="btn-floating btn-large red">
				<i class="fa fa-pencil"></i>
			</router-link>
		</div>
	</div>
</template>

<script>
import db from '../firebase/init'
export default {
  name: 'ViewEmployee',
  data () {
    return {
      employee_id: null,
      first_name: null,
      last_name: null,
      department: null,
      position: null
    }
  },
  beforeRouteEnter (to, from, next) {
    db.collection('employees').where('employee_id', '==', to.params.employee_id)
    .get().then(querySnapShot => {
      querySnapShot.forEach(doc => {
        let data = doc.data();
        next(vm => {
          vm.employee_id = data.employee_id,
          vm.first_name = data.first_name,
          vm.last_name = data.last_name,
          vm.department = data.department,
          vm.position = data.position
        })
      })
    })
  },
  watch: {
    '$route': 'fetchData'
  },
  methods: {
    fetchData() {
      db.collection('employees').where('employee_id', '==', this.$route.params.employee_id)
      .get().then(querySnapShot => {
        querySnapShot.forEach(doc => {
          let data = doc.data()
          this.employee_id = data.employee_id
          this.first_name = data.first_name
          this.last_name = data.last_name
          this.department = data.department
          this.position = data.position
        })
      })
    },
    deleteEmployee() {
      if(confirm('Confirm delete action for employee id ' + this.employee_id) != true) return

      db.collection('employees')
      .where('employee_id', '==', this.employee_id)
      .get()
      .then(querySnapShot => {
        querySnapShot.forEach(doc => {
          doc.ref.delete()
          this.$router.push('/')
        })
      })
    }
  },
  computed: {
    editLink() {
      return { name: 'edit-employee', params: { employee_id: this.employee_id } }
    }
  },
}
</script>

<style lang="css" scoped>
</style>


